<template>
  <div class="shop-audit">
    <h2>店铺资质审核</h2>
    <el-table :data="pendingShops" style="width: 100%">
      <el-table-column prop="name" label="店铺名称" />
      <el-table-column prop="username" label="店主账号" />
      <el-table-column label="资质状态">
        <template #default="scope">
          <el-tag :type="scope.row.qualificationState ? 'success' : 'danger'">
            {{ scope.row.qualificationState ? "已通过" : "未通过" }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="资质图片">
        <template #default="scope">
          <el-image
            style="width: 100px; height: 100px"
            :src="scope.row.imgUrl"
            :preview-src-list="[scope.row.imgUrl]"
            fit="cover"
          />
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template #default="scope">
          <el-button-group>
            <el-button
              size="small"
              type="primary"
              @click="handleQualificationApprove(scope.row)"
              >通过</el-button
            >
            <el-button
              size="small"
              type="danger"
              @click="handleQualificationReject(scope.row)"
              >拒绝</el-button
            >
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script setup>
import { ref, onMounted } from "vue";
import { get, patch } from "@/utils/request";
import { ElMessage } from "element-plus";

const pendingShops = ref([]);

const fetchPendingShops = async () => {
  try {
    const res = await get("/api/shop-check");
    pendingShops.value = res.data || [];
  } catch (error) {
    ElMessage.error("获取待审核店铺失败");
  }
};

const handleQualificationApprove = async shop => {
  try {
    await patch(`api/shop-check/qualification/${shop.id}`, { state: 1 });
    ElMessage.success("资质审核通过");
    fetchPendingShops();
  } catch (error) {
    ElMessage.error("操作失败");
  }
};

const handleQualificationReject = async shop => {
  try {
    await patch(`api/shop-check/qualification/${shop.id}`, { state: 0 });
    ElMessage.success("资质审核已拒绝");
    fetchPendingShops();
  } catch (error) {
    ElMessage.error("操作失败");
  }
};

onMounted(() => {
  fetchPendingShops();
});
</script>

<style scoped>
.shop-audit {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.el-image {
  cursor: pointer;
}

.el-button-group {
  margin-right: 10px;
}
</style>
